import data from "../data/data.json";

const getMovies = (code) => {
  if (!!code)
    return data.movies.filter((movie) => movie.catagories.includes(code));
  return data.movies;
};

const getMovieById = (movieId) => {
  return data.movies.filter((movie) => movie.id === +movieId).pop();
};

const getCatagories = () => {
  return data.hebCatagories;
};

export const movieService = {
  getMovies,
  getMovieById,
  getCatagories,
};
