import { useState, useEffect } from "react";
// import { useParams, useSearchParams } from "react-router-dom";
import { movieService } from "../services/movie.service";

import { MovieList } from "../cmps/movies-main/movie-list";
import { Filter } from "../cmps/movies-main/filter/filter";
import { Header } from "../cmps/header";
import { Footer } from "../cmps/footer";
import { Message } from "../cmps/movies-main/message";

export const MoviesMain = () => {
  // const [searchParams, setSearchParams] = useSearchParams();
  const [movies, setMovies] = useState([]);
  const [selectedCatagory, setCatagory] = useState(0);
  // const { catagory } = useParams();

  useEffect(() => {
    // const urlCatagory = searchParams.get("catagory");
    // console.log(catagory);
    // console.log("searchParams", searchParams);
    const data = movieService.getMovies(selectedCatagory);
    setMovies(data);
    window.scrollTo(0, 0);
    // if (catagory) setSearchParams({ catagory });
    // else setSearchParams({});
  }, [selectedCatagory]);

  return (
    <>
      <Header />
      <main className="movies-main">
        <Message />
        <Filter setCatagory={setCatagory} currCat={selectedCatagory} />
        {movies.length && <MovieList movies={movies} />}
      </main>
      <Footer />
    </>
  );
};
