import { Home } from "./pages/home";
import { MoviesMain } from "./pages/movies-main";
import { MovieDetails } from "./pages/movie-details";

export const routes = [
  {
    path: "/movies",
    component: MoviesMain,
  },
  {
    path: "/movie/:movieId",
    component: MovieDetails,
  },
  {
    path: "/",
    component: Home,
  },
];
