import { MovieLength } from "./movie-info-cmps/movie-length";

import { Btns } from "./movie-info-cmps/btns";
import { CatagoriesList } from "./movie-info-cmps/catagories-list";
import { MovieDescription } from "./movie-info-cmps/movie-description";
import { MovieActivity } from "./movie-info-cmps/movie-activity";

export const MovieInfo = ({ movie, play, onPlay }) => {
  const {
    title,
    length,
    catagories,
    fullDes,
    shortDes,
    credits,
    extraDetails,
    activities,
  } = movie;

  return (
    <div className="info">
      <h2 className="title">{title}</h2>
      <MovieLength length={length} />
      <CatagoriesList catagories={catagories} />
      <MovieDescription full={fullDes} short={shortDes} credits={credits} />
      {extraDetails && <p className="extra-details">{extraDetails}</p>}
      {activities?.length && <MovieActivity activities={activities} />}
      <Btns play={play} onPlay={onPlay} />
    </div>
  );
};
