import { useNavigate } from "react-router-dom";

export const Header = () => {
  const navigate = useNavigate();

  return (
    <header>
      {/* <div className="div"></div> */}
      <div className="img-container" onClick={() => navigate("/")}>
        <img src={require("../assets/imgs/365logo.png")} alt="logo" />
      </div>
      {/* <div className="div"></div> */}
    </header>
  );
};
