import { useState } from "react";
import { useNavigate } from "react-router-dom";

import { HeaderHome } from "../cmps/home-cmps/header-home";
import { Info } from "../cmps/home-cmps/info";
import { MovieMonth } from "../cmps/home-cmps/movie-month";
import { AboutUs } from "../cmps/home-cmps/about-us";
import { CallToAction } from "../cmps/home-cmps/call-to-action";
import { Footer } from "../cmps/footer";

export const Home = () => {
  const [shaddow, setShaddow] = useState(false);
  const navigate = useNavigate();
  setTimeout(() => setShaddow(true), 2000);

  const navToMovie = () => {
    navigate(`/movie/1004`);
  };

  const navToMovies = () => {
    navigate("/movies");
  };

  return (
    <section className="home">
      <HeaderHome />
      <Info shaddow={shaddow} navToMovies={navToMovies} />
      <MovieMonth navToMovie={navToMovie} />
      <AboutUs />
      <CallToAction navToMovies={navToMovies} />
      <Footer />
    </section>
  );
};
