export const MovieLength = ({ length }) => {
  const { hour } = length;

  switch (typeof hour) {
    case "object":
      return (
        <span className="length">
          {length.min}:{length.sec} 
        </span>
      );

    default:
      return (
        <span className="length">
          {length.hour}:{length.min} 
        </span>
      );
  }
};
