export const MovieDescription = ({ full, short, credits }) => {
  return (
    <div className="des">
      <p className="full-des">{full ? full : short}</p>
      {credits && (
        <div className="credits">
          {credits.map(([job, person]) => (
            <p className="credit" key={job}>
              {job}: {person}
            </p>
          ))}
        </div>
      )}
    </div>
  );
};
