import Jump from "react-reveal/Jump";

export const CallToAction = ({ navToMovies }) => {
  return (
    <div className="call-to-action">
      <Jump duration={2000}>
        <div className="btn-border">
          <button className="btn-call" onClick={navToMovies}>
            
          </button>
        </div>
      </Jump>
      <h4>  ,   ...</h4>
    </div>
  );
};
