import { useNavigate } from "react-router-dom";

export const Btns = ({ play, onPlay }) => {
  const navigate = useNavigate();

  return (
    <aside className="btns-container">
      {!play && (
        <button className="watch" onClick={onPlay}>
           
        </button>
      )}
      <button className="back" onClick={() => navigate("/movies")}>
          
      </button>
    </aside>
  );
};
