export const MovieContainer = ({ movie, play, height }) => {
  return (
    <div className="movie-container">
      <div
        className="img-container"
        style={{ display: play ? "none" : "block" }}
      >
        <img src={movie.coverImg} alt="Movie preview" />
      </div>
      <div
        className="video-container"
        style={{ display: play ? "block" : "none" }}
      >
        <iframe
          src={movie.url}
          title={movie.title}
          frameBorder="0"
          width="100%"
          height={height}
          allowFullScreen
        ></iframe>
      </div>
    </div>
  );
};
