import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

import { movieService } from "../services/movie.service";

import { MovieInfo } from "../cmps/movie-details-cmps/movie-info";
import { MovieContainer } from "../cmps/movie-details-cmps/movie-container";
import { Header } from "../cmps/header";
import { Footer } from "../cmps/footer";

export const MovieDetails = () => {
  const { movieId } = useParams();
  const [movie, setMovie] = useState(null);
  const [play, setPlay] = useState(false);
  const [height, setHeight] = useState("0px");

  useEffect(() => {
    _loadMovie(movieId);
  }, [movieId]);

  const _loadMovie = (movieId) => {
    setMovie(movieService.getMovieById(movieId));
    setHeight((window.innerHeight - 135) / 2 + "px");
  };

  const onPlay = () => {
    setPlay(true);
    window.scrollTo(0, 0);
  };

  return (
    <>
      <Header />
      {movie && (
        <section
          className="movie-details"
          // style={{ backgroundColor: "rgba(0, 0, 0, 0.025)" }}
        >
          <MovieInfo movie={movie} play={play} onPlay={onPlay} />
          <MovieContainer movie={movie} play={play} height={height} />
        </section>
      )}
      <Footer />
    </>
  );
};
