import Flip from "react-reveal/Flip";
import Fade from "react-reveal/Fade";

import { IActivity } from "../../../assets/style/icons";

export const MovieActivity = ({ activities }) => {
  return (
    <Flip bottom duration={1500}>
      <section className="movie-activities">
        {" "}
        <div className="title">
          <IActivity />
          <h3 className="title">  </h3>
        </div>
          <ul className="acts-list clean-list">
            {activities.map((act) => (
              <li className="act-preview" key={act}>
                <span>{act}</span>
              </li>
            ))}
          </ul>
      </section>
    </Flip>
  );
};
