import { CatagoryPreview } from "./catagory-preview";

export const CatagoriesList = ({ catagories }) => {
  return (
    <div className="catagories">
      {catagories.map((movieCatCode) => (
        <CatagoryPreview key={movieCatCode} movieCatCode={movieCatCode} />
      ))}
    </div>
  );
};
