import { movieService } from "../../../services/movie.service";

export const CatagoryPreview = ({ movieCatCode }) => {
  const catagories = movieService.getCatagories();
  const catagory = catagories.filter(([code, value]) => +movieCatCode === code);

  return <div className="catagory">{catagory[0][1]}</div>;
};
